.header {
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: white;
  position: sticky;
  top: 0;
  z-index: 1;
}

.header__logo {
  width: 100px;
  object-fit: contain;
  margin: 0 20px;

  height: 50px;
}

.header__nav {
  display: flex;
  justify-content: space-evenly;
  text-decoration: none;
}

.header__option {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  margin-left: 10px;
  margin-right: 10px;
  color: black;
  text-decoration: none;
}

.header__optionLineOne {
  font-size: 15px;
  cursor: pointer;
}

a {
  text-decoration: none;
  color: black;
  font-weight: 700;
}
