.table {
  margin-top: 40px;
  overflow: scroll;
  height: 400px;
  color: #6a5d5d;
  flex-direction: column;
  margin-bottom: 30px;
}

.table tr {
  display: flex;
  justify-content: space-between;
}

.table td {
  padding: 0.9rem;
}

.table tr:nth-of-type(odd) {
  background-color: #f3f2f8;
}
